@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.app {
  color: white;
  background-color:  rgb(33, 33, 37);
  min-width: 360px;
  max-width: 1920;
}

p {
  font-size: 20px;
  line-height: 1.5;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    background-color: #212125;
    height: 72px;
    padding: 0px 30px;
    margin: 0 auto;
    max-width: 1400px;
} 
.logo {
    margin-top: 4px;
}

svg #discord-logo {
    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

svg:hover #discord-logo {
    fill: #7289da;
}
.menu {
    display: flex;
    width: 100%;
}

.menu ul{
    display: flex;
    justify-content: space-between;
    align-items: stretch; 
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.menu li a{
    display: block;
    padding: 0px 30px;
    color: white;
    text-decoration: none;
    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.menu li a:hover{
    color: #7289da;
}

@media screen and (max-width: 560px) {
    div.menu {
      display: none;
    }
}
.language-button {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 120px;
    height: 50px;
    padding-right: 0;
    border: 0;
    background-color: transparent;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    color: rgb(255, 255, 255, 0.5);
}

.separator {
    margin: 0px 10px;
    width: 2px;
    height: 20px;
    background-color: rgb(255, 255, 255, 0.5);
}

.language-button svg #language-icon {
    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.language-button svg:hover #language-icon {
    fill: #7289da;
}
.language-switcher {
    position: absolute;
    display: inline-block;
    top: 72px;
    right: 30px;
    padding: 0;
    z-index: 999;

    width: 160px;
    height: 100px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0px 0px 2px rgb(197, 197, 197);
    -webkit-animation: fade-bottom ease-in-out 0.2s;
            animation: fade-bottom ease-in-out 0.2s;
}

@-webkit-keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@keyframes fade-bottom {
    from {
      opacity: 0;
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

.language-switcher ul {
    padding: 0;
    margin: 0;
}

.language-switcher li {
    list-style-type: none;
    text-align: left;
    cursor: pointer;
    display: flex;
    align-items: center;
    height: 50px;
    color: black;

    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.language-switcher li svg {
    padding: 0px 13px 0px 13px;
}

.language-switcher li:hover {
    color: #7289da;
    background-color: #f2f2f2;
    border-radius: 5px;
}

div.sroll-down {
    display: none;
    background-color: #7289da;
    height: 64px;
    padding: 0px 30px;
    color: white;
    justify-content: space-between;
    align-items: center;
}

.commands-button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 30px;
  border-radius: 3px;
  text-decoration: none;
  color: white;
  background-color: #5f76ca;


  transition: 200ms;
  transition-timing-function: ease-in-out;
  -webkit-transition: 200ms;
  -webkit-transition-timing-function: ease-in-out;
}

.commands-button:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

@media screen and (max-width: 560px) {
    div.menu {
      display: none;
    }
    div.sroll-down {
      display: flex;
    }
}
.add-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 48px 0px;
    background-color: #26262C;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0px auto;
    width: 80%;
    max-width: 1024px;
    min-width: 320px;
}

code.tag {
    display: inline-block;
    margin-left: 5px;
    padding: 0px 5px;
    border-radius: 5px;
    background-color: #393E41;
}

.info {
    text-align: left;
    flex-wrap: wrap;
}

.button-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 48px;
}

@media screen and (max-width: 992px) {
    div.info {
        text-align: center;
    }
    div.button-wrapper {
        margin-top: 20px;
    }
}

@media screen and (max-width: 560px) {
    div.button-wrapper {
        flex-direction: column;
    }
}


#newser-logo {
    padding: 0px 100px;
}
.add-button {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 45px;
    width: 240px;
    border-radius: 3px;
    margin: 5px 10px;

    color: white;
    background-color: #7289da;
    text-decoration: none;
    box-shadow: 0px 0px 5px rgb(41, 41, 41);

    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.add-button:hover {
    background-color: #596db4;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}
.download-button {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 45px;
    width: 240px;
    border-radius: 3px;
    margin: 5px 10px;

    color: black;
    background-color: white;
    text-decoration: none;
    box-shadow: 0px 0px 5px rgb(41, 41, 41);

    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.download-button:hover {
    color: #596db4;
    background-color: #f2f2f2;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}
.command-section {
    margin: 0 auto;
    padding: 0px 30px;
    max-width: 768px;
}

.command-section :nth-child(9) .discription {
    border-bottom: none;
}

#commands {
    text-align: left;
}
.command{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 10px;
}

.command-name {
    display: flex;
    min-width: 140px;
}

.commandID {
    display: inline-block;
    padding: 2px 5px;
    border-radius: 5px;
    background-color: #393E41;
    font-size: 20px;
    font-family: 'IBM Plex Mono', monospace;
}

.prefix {
    color: #7289da;
}

.discription {
    display: block;
    width: 100%;
    text-align: left;
    word-wrap: break-word;
    border-bottom: solid 0.5px rgb(255, 255, 255, 0.5);
}

.message-sample {
    display: inline-block;
    word-break: break-word;
    padding: 2px 5px;
    border-radius: 5px;
    background-color: #7289da;
    font-family: 'IBM Plex Mono', monospace;
    min-width: 50px;
}
.about-section {
    background-color: #26262C;
}

.about-content {
    margin: 0 auto;
    padding: 30px 30px;
    max-width: 768px;
}


.social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    padding: 30px;
    background-color: #26262C;
}

.copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 48px;
    padding: 10px 30px;
}

.copyright a {
    text-decoration: none;
    color: #7289da;
    transition: 200ms;
    transition-timing-function: ease-in-out;
    -webkit-transition: 200ms;
    -webkit-transition-timing-function: ease-in-out;
}

.copyright a:hover {
    color: #596db4;
}
